import { _decorator, Component, instantiate, Node } from 'cc';
import { item3 } from './item3';
const { ccclass, property } = _decorator;

type itemData ={
    itemName:string;
    itemDesc:string

};


@ccclass('TestItemCreate')
export class TestItemCreate extends Component {

    itemDataArray:itemData[];

    @property(Node)
    contentNode:Node=null;

    @property(item3)
    itemPrefab:item3=null;


    start() {
        
        for(let i=0;i<300;i++){
            let data:itemData={itemName:"name "+i.toString,itemDesc:"the desc "+i.toString};
            this.itemDataArray.push(data);

        }

        this.itemDataArray.forEach(element => {
            let ins=instantiate(this.itemPrefab);
            ins.setValue(element.itemName,element.itemDesc);

            this.contentNode.addChild(ins.node);
        });





    }

    update(deltaTime: number) {
        
    }
}

